@import url(https://fonts.googleapis.com/css?family=Noto+Sans+TC:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{
    background-color: aliceblue;
    grid-column: 1 / span 2;
    grid-row: 1;
}
.navbar .flex{
    display: flex;
    height: 80%;
    flex-direction: row;
}
.navbar .flex div{
    margin: auto;
}
.navbar *{
    margin: 5px;
}
.navbar div{
    display: inline-block;
    padding: 0 30px;
}
.navbar img{
    height: 50%;
    display: inline-block;
}
.sidebar {
    background-color: aliceblue;
    grid-column: 1;
    grid-row: 2 / span 999;
}
.sidebar a{
    display: block;
    padding: 20px 0;
    font-size: 24px;
}
.payment-info{
    list-style: none;
    font-size: large;
    text-align: left;
    background-image: url(data:image/gif;base64,);
}
.payment-info h2{
    font-size: 24px;
    font-weight: bold;
}
.payment-info ul{
    margin: 20px;
}
.payment-info li{
    padding: 10px;
}
.red{
    color: red;
}
.blue{
    color: blue;
}
.line{
    margin: 20px;
}
.news{
    text-align: left;
}
.announces{
    text-align: left;
}
.news h1, .news img, .news ul{
    margin: 20px 30px;
}
.prices p{
    display: inline-block;
    color:blue;
    font-weight: bold;
}
.prices li, .prices p{
    margin: 10px;
    font-size: 24px;
}
.h1{
    font-size: 36px;
}
.news ul li{
    margin: 10px;
}
.questions{
    text-align: left;
}
.questions h1{
    color: crimson;
}
.questions h2{
    color: crimson;
    font-size: 24px;
}
.questions ul{
    list-style: none;
    margin: 40px 10px;
    max-width: 70%;
}
.questions li{
    margin: 10px;
    line-height: 150%;
}
.questions li h3 {
    font-weight: bold;
}
.about{
    width: 80%;
    margin: 5% auto;
    text-align: left;
}
.section1{
    display: grid;
    grid-template-columns: 50% 50%;
}
.section1 h3{
    font-size: 24px;
}
.section1 p{
    font-size: 18px;
}
.intro{
    grid-row: 1;
    grid-column: 1;
}
.intro h3{
    color: blue;
}
.section1 img:first-child{
    grid-column: 2;
}
#outside1{
    grid-row: 1;
    grid-column: 2;
    width: 90%;
}
#outside2{
    grid-row: 2;
    grid-column: 1;
    width: 90%;
}
#namecard{
    max-width: 110%;
    height: auto;
    grid-row: 2;
    grid-column: 2;
}
.section2 img{
    width: 48%;
    padding: 5px;
}
.section2 img:last-child{
    width: 110%;
}
.section3 li{
    font-size: 18px;
    line-height: 160%;
    margin: 20px 0;
}
.grip{
    margin: 20px;
}
.grip img{
    display: block;
    margin: auto;
    padding: 50px;
}

.item{
    text-align: left;
    width: 80%;
    margin: auto;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    border: 2px gray solid;
    display: grid;
    grid-template-columns: 10% auto;
}
.item img{
    max-width: 50%;
}
.item b{
    display: inline-block;
    font-weight: bold;
}
.item div, .item p{
    margin-bottom: 2%;
}
.item h3{
    color: blue;
}
.column1{
    grid-column: 1;
}
.column2{
    grid-column: 2;
}
.App {
  text-align: center;
  width: 100vw;
  height: 100vh;
  display: grid;
  grid-template-columns: 20% auto;
  grid-template-rows: 20% auto;
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
	
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

h1{
    font-size: 36px;
}

h2{
    font-size: 24px;
}
h3{
    font-size: 18px
}
p {
    font-size: 14px
}
h1, h2, h3, h4, p, ul, li, div, a{
    font-family: 'Noto Sans TC', sans-serif;
}
